import { Button } from "antd";
import React, { useCallback, useState } from "react";
// import logo from './logo.svg';
import "./App.css";
// import Excel from './excel';
import routes from "./router";
import { useRoutes } from "react-router";
import { useNavigate } from "react-router-dom";
// const Excel = React.lazy(
//   () => import(/* webpackChunkName: "lazyComponent"*/ "./excel")
// );

function App() {
  const [messages] = useState([
    { id: "001", title: "1", content: "", hishty: "demo" },
    { id: "002", title: "2", content: "", hishty: "details" },
    { id: "003", title: "3", content: "", hishty: "mine" },
    { id: "004", title: "4", content: "", hishty: "GGGG" },
  ]);
  const [store, setStore] = useState(false);
  const navigate = useNavigate();

  const element = useRoutes(routes);
  const debounce = (func: (arg0: any) => void, wait = 800) => {
    let timeout: string | number | NodeJS.Timeout | undefined;
    return function (e: any) {
      clearTimeout(timeout); // ,
      timeout = setTimeout(() => {
        func(e);
      }, wait);
      // ms, ;
    };
  };

  const Buttons = useCallback(() => {
    const load = useCallback(
      debounce((e) => toParams(e), 500),
      []
    );
    const toParams = (item: {
      id: any;
      title: any;
      content: any;
      hishty: any;
    }) => {
      console.log(1);

      const { id, title, content, hishty } = item;
      navigate(hishty, {
        replace: false,
        state: {
          id: id,
          title: title,
          content: content,
        },
      });
    };

    return (
      <>
        {messages.map((item) => {
          return (
            <div key={item.id}>
              <Button onClick={() => load(item)}>{item.hishty}</Button>
            </div>
          );
        })}
      </>
    );
  }, [messages]);

  const yincang = () => {
    setStore(!store);
  };

  return (
    <div className="App">
      <React.Suspense fallback={<div></div>}>
        <div hidden={store}>
          <Buttons />
        </div>
        <Button onClick={yincang}>{!store ? "" : ""}</Button>
        <Button
          onClick={() => {
            navigate("");
          }}
        >
          home
        </Button>
        {element}
      </React.Suspense>
    </div>
  );
}

export default App;
